.team {
	padding: 90px 0 81px;
	color: $black;
	background: $white;
	text-align: center;
	@include lg {
		padding: 60px 0; }
	@include sm {
		padding: 50px 0 20px; }
	&__title {
		margin-bottom: 32px;
		text-align: center;
		@include lg {
			margin-bottom: 40px; }
		@include sm {
			margin-bottom: 33px; } }
	&__svg {
		position: relative;
		overflow: hidden;
		svg {
			position: absolute;
			top: 0;
			left: 0;
			width: 248px;
			height: 248px; } }
	&__members {
		margin: 0 -15px;
		@include clr;
		@include md {
			margin: 0 -25px; } }
	&__info {
		margin-top: 91px;
		@include md {
			margin-top: 68px; }
		@include sm {
			margin-top: 45px; } }
	&__member {
		float: left;
		width: 33.33%;
		@include sm {
			float: none;
			margin: 0 auto;
			width: 100%;
			max-width: 290px; } }
	&__row {
		margin: 0 auto;
		max-width: 1087px;
		@include clr;
		// +lg
		/// max-width: 487px
		// +md
 }		/// max-width: 480px
	&__col {
		// float: left
		// width: 50%
		// padding: 0 0 0 55px
		max-width: 888px;
		margin: 0 auto;
		&:first-child {
			// padding: 0 60px 0 0
			@include md {
				padding: 0; } }
		// &:not(:first-child)
		// 	+md
		// 		margin-top: 32px
		// 	+sm
		// 		margin-top: 28px
		@include lg {
			width: 100%;
			padding: 25px 0; }
		@include md {
			padding: 15px 0; } }
	&__subtitle {
		margin-bottom: 30px;
		@include f(18, 22, null, 700);
		@include lg {
			margin-bottom: 23px; }
		@include md {
			margin-bottom: 38px; }
		@include sm {
			margin-bottom: 26px; } }
	&__partners {
		padding: 59px 20px 24px;
		font-size: 0;
		@include clr;
		@include md {
			padding: 50px 0 25px; } }
	&__partner {
		display: inline-block;
		vertical-align: top;
		width: 20%;
		@include md {
			width: 33.33%; }
		@include sm {
			width: 50%; }
		@include xs {
			width: 100%; }
		img {
			width: 100%;
			max-width: 220px; } } }
