.btn {
	display: inline-block;
	vertical-align: top;
	padding: 12px 20px 15px;
	border-radius: 5px;
	text-align: center;
	color: $white;
	background: $green;
	font-size: 20px;
	line-height: 23px;
	letter-spacing: -0.02em;
	transition: background-color .3s ease-in-out;
	cursor: pointer;
	@include hover {
		background-color: darken($green, 10%); }
	&_blue {
		padding: 15px 40px 16px;
		font-size: 16px;
		font-weight: bold;
		line-height: 19px;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		background-color: #00C5DF;
		@include hover {
			background-color: darken(#00C5DF, 10%); } }
	&_christi {
		background-color: $christi;
		@include hover {
			background-color: darken($christi, 10%); } }
	&:disabled {
		background-color: $lightGray;
		pointer-events: none; } }
