.info-block {
	padding: 213px 0 48px;
	color: $white;
	@include lg {
		padding: 100px 0 60px; }
	@include sm {
		padding: 50px 0 28px;
		text-align: center; }
	&__header {
		position: relative;
		margin: 0 auto 66px;
		text-align: left;
		@include lg {
			margin-bottom: 30px; }
		@include sm {
			margin-bottom: 40px;
			text-align: center; }
		&:before {
			content: '';
			position: absolute;
			top: -68px;
			left: -151px;
			width: 276px;
			height: 296px;
			background: url(../img/info-block-bg.png) no-repeat center center;
			background-size: contain;
			@include sm {
				display: none; } }
		&-in {
			overflow: hidden;
			padding: 9px 0 0;
			max-width: 770px; } }
	&__title {
		margin-bottom: 21px;
		text-align: left;
		@include lg {
			margin-bottom: 22px; }
		@include sm {
			margin-bottom: 17px;
			text-align: center; } }
	&__logo {
		float: left;
		margin: 0 38px 11px 0;
		width: 120px;
		@include sm {
			float: none;
			display: inline-block;
			vertical-align: top;
			margin-bottom: 22px;
			width: 60px; } }
	&__content {
		margin: 0 auto 248px;
		text-align: left;
		@include lg {
			margin-bottom: 60px; }
		@include sm {
			margin-bottom: 30px;
			text-align: center; } }
	&__row {
		padding: 5px 0 7px;
		margin: 0 33px 0 2px;
		@include sm {
			padding: 16px 0 17px; }
		&:not(:first-child) {
			border-top: 1px solid rgba($white, 0.2); } }
	&__graph {
		padding: 20px 0 15px;
		margin: 0 auto;
		max-width: 970px;
		@include lg {
			padding: 40px 0;
			max-width: 597px; }
		@include md {
			padding: 18px 0; }
		@include sm {
			// max-width: 100%
			max-width: 400px; } } }
