.features {
	padding: 49px 0 74px;
	@include md {
		padding: 53px 0;
		text-align: center; }
	@include sm {
		padding: 40px 0; }
	&__title {
		&:not(:last-child) {
			margin-bottom: 26px; } }
	// &__sm-description
	// 	margin: 0 auto
	// 	max-width: 970px
	&__sm-description {
		max-width: 704px;
		@include md {
			max-width: 100%; } }
	&__wrap {
		margin: 0 -30px;
		font-size: 0;
		@include clr;
		@include sm {
			margin: 0; }
		&:not(:first-child) {
			margin-top: 67px;
			@include md {
				margin-top: 98px; }
			@include sm {
				margin-top: 53px; } } }
	&__col {
		margin-bottom: 50px;
		padding: 0 30px;
		width: 33.3%;
		font-size: 16px;
		display: inline-block;
		vertical-align: top;
		@include md {
			margin-bottom: 47px;
			float: none;
			width: 100%; }
		@include sm {
			margin-bottom: 28px;
			padding: 0;
			font-size: 14px;
			line-height: (22/14); } }
	&__ico {
		@include md {
			vertical-align: top;
			display: inline-block; }
		&_stars {
			width: 85px; }
		&_ppl {
			width: 57px; }
		&_coins {
			width: 53px; }
		&_robo {
			width: 61px; }
		&_net {
			width: 68px; }
		&_clouds {
			width: 61px; }
		&:not(:last-child) {
			margin-bottom: 25px;
			@include md {
				margin-bottom: 14px; }
			@include sm {
				margin-bottom: 19px; } }
		img {
			max-width: 100%;
			display: block; } }
	&__title-md {
		@include f(26, 25, null, 700);
		&:not(:last-child) {
			margin-bottom: 20px;
			@include md {
				margin-bottom: 18px; }
			@include sm {
				margin-bottom: 21px;
				font-size: 20px; } } }
	&__formula {
		margin: 15px auto 0;
		max-width: 180px;
		img {
			max-width: 100%;
			display: block; } } }
