.topic {
	position: relative;
	padding: 10.4493vh 0 200px;
	min-height: 100vh;
	@include lg {
		padding: 47px 0 55px;
		min-height: initial; }
	@include sm {
		padding: 22px 0 47px; }
	&__inner {
		padding-top: 60px;
		@include lg {
			padding-top: 0; } }
	&__logo {
		float: left;
		padding: 16px 0 0;
		width: 150px;
		font-size: 0;
		text-align: center;
		@include lg {
			float: none;
			padding: 0 0 47px; }
		@include md {
			padding-bottom: 62px; }
		@include sm {
			padding-bottom: 34px;
			width: 90px; }
		img {
			max-width: 100%;
			@include lg {
				width: 68px; }
			@include sm {
				width: 42px; } } }
	&__title {
		margin-top: 28px;
		@include f(32, 37, null, 700);
		letter-spacing: 0.08em;
		@include lg {
			margin-top: 30px; }
		@include sm {
			margin-top: 13px;
			@include f(16, 19); } }
	&__content {
		overflow: hidden;
		padding: 0 103px 0 116px;
		@include lg {
			padding: 0 0 64px; }
		@include md {
			padding-bottom: 7vh; }
		@include sm {
			padding-bottom: 10px; } }
	&__sub-title {
		margin-bottom: 6vh;
		@include f(60, 74, null, 700);
		@include md {
			margin-bottom: 18px;
			@include f(46, 54); }
		@include sm {
			@include f(26, 30); } }
	&__description {
		max-width: 570px;
		font-size: 26px;
		line-height: 1.42;
		@include sm {
			@include f(16, 22); }
		p {
			padding: 10px 0;
			@include sm {
				padding: 5px 0; } } }
	&__subscription {
		margin-bottom: 12px;
		float: left;
		width: 100%;
		max-width: 467px;
		@include lg {
			max-width: 467px; }
		@include sm {
			position: relative;
			padding: 21px 0 35px;
			max-width: 100%; }
		@include xxs {
			padding: 13px 0 27px; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 50%;
			bottom: 0;
			display: none;
			width: 2000px;
			background: rgba($black, 0.2);
			transform: translate3d(-50%,0,0);
			@include sm {
				display: block; } }
		.subscription__label,
		.field {
			@include sm {
				position: relative;
				z-index: 2; } }
		.field {
			@include xxs {
				padding-bottom: 62px; } } }
	&__bottom {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 88px;
		@include rh(630) {
			bottom: 44px; }
		@include lg {
			position: relative;
			left: auto;
			right: auto;
			bottom: auto;
			margin-top: 188px; }
		@include md {
			margin-top: 9vh; }
		@include sm {
			margin-top: 19px; }
		.inner {
			display: table;
			width: 100%;
			@include lg {
				display: block; } } }
	&__t-coll {
		vertical-align: bottom;
		display: table-cell;
		@include clr;
		& + .topic__t-coll {
			margin-top: 45px;
			@include sm {
				margin-top: 17px; } }
		@include lg {
			display: block; } }
	.inner {
		@include lg {
			margin: 0 auto;
			max-width: 850px; }
		@include md {
			padding: 0 48px 0 128px;
			max-width: 768px; }
		@include sm {
			padding: 0 30px;
			max-width: 100%; }
		@include xxs {
			padding: 0 26px 0 31px; } } }

.info {
	position: relative;
	padding-left: 22px;
	vertical-align: top;
	letter-spacing: 0.17em;
	font-size: 12px;
	color: rgba($white, .5);
	text-transform: uppercase;
	display: inline-block;
	@include sm {
		padding: 0; }
	&__icon {
		position: absolute;
		top: -1px;
		left: 0;
		@include sm {
			position: relative;
			top: 0;
			text-align: center; }
		.icon {
			font-size: 20px; } }
	&__text {
		@include sm {
			margin: 0 auto;
			max-width: 205px; } } }
.creators {
	&__text {
		margin-top: 10px;
		@include f(12, 24);
		letter-spacing: 0.17em;
		text-transform: uppercase;
		@include md {
			margin-top: 0;
			display: block; } }
	&__text,
	&__list {
		vertical-align: top;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 40px;
			@include md {
				margin-left: 0; } } }
	&__list {
		@include md {
			margin-top: 10px;
			display: block; } }
	&__item {
		vertical-align: middle;
		font-size: 0;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 22px;
			@include sm {
				margin-left: 7px; } }
		&_text {
			font-size: 14px; } } }

.clouds {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 1470px;
	perspective: 1000;
	-webkit-perspective: 1000;
	-moz-perspective: 1000;
	-o-perspective: 1000;
	.is-firefox & {
		perspective: 500px;
		-webkit-perspective: 500px;
		-moz-perspective: 500px;
		-o-perspective: 500px; }
	&__word {
		position: absolute;
		top: 0;
		left: 50%;
		margin-left: -850px;
		width: 1700px;
		height: 1470px;
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		-moz-transform-style: preserve-3d;
		-o-transform-style: preserve-3d; }
	[class^="cloudBase-"] {
		position: absolute;
		margin-left: -10px;
		margin-top: -10px;
		width: 20px;
		height: 20px;
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		-moz-transform-style: preserve-3d;
		-o-transform-style: preserve-3d; }
	.cloudBase-1 {
		top: 150px;
		left: 30%; }
	.cloudBase-2 {
		top: 200px;
		left: 40%; }
	.cloudBase-3 {
		top: 250px;
		left: 50%; }
	.cloudBase-4 {
		top: 200px;
		left: 60%; }
	.cloudBase-5 {
		top: 150px;
		left: 70%; }

	.cloudBase-6 {
		top: 830px;
		left: 20%; }
	.cloudBase-7 {
		top: 830px;
		left: 30%; }
	.cloudBase-8 {
		top: 830px;
		left: 60%; }
	.cloudBase-9 {
		top: 830px;
		left: 70%; }

	.cloudLayer {
		position: absolute;
		left: 50%;
		top: 50%;
		width: 256px;
		height: 256px;
		margin-left: -128px;
		margin-top: -128px;
		-webkit-transition: opacity .5s ease-out;
		-moz-transition: opacity .5s ease-out;
		-o-transition: opacity .5s ease-out; } }
