.sm-graph {
	&:not(:last-child) {
		@include md {
			margin-bottom: 41px; }
		@include sm {
			margin-bottom: 0; } }
	&_green {
		.sm-graph {
			&__title {
				color: #7EAC0E; }
			&__line {
				&:before, &-in {
					background: #7EAC0E; } }
			&__box {
				&:nth-child(even) {
					.sm-graph {
						&__line {
							&:before, &-in {
								background: #334406; } } } } } } }
	&_blue {
		.sm-graph {
			&__title {
				color: $blue; }
			&__line {
				&:before, &-in {
					background: #1B5580; } }
			&__box {
				&:nth-child(even) {
					.sm-graph {
						&__line {
							&:before, &-in {
								background: #0E2B41; } } } } } } }
	&__title {
		margin-bottom: 20px;
		@include f(26, 31, null, 700);
		@include lg {
			margin-bottom: 50px; }
		@include sm {
			margin-bottom: 28px;
			@include f(16, 24); } }
	&__container {
		display: table;
		table-layout: fixed;
		width: 100%; }
	&__row {
		display: table-row; }
	&__box {
		display: table-cell;
		min-width: 67px;
		vertical-align: top;
		@include lg {
			width: 100% !important;
			margin-bottom: 4px; } }
	&__container,
	&__row,
	&__box {
		position: relative;
		@include lg {
			display: block;
			text-align: left; } }
	&__line {
		position: relative;
		padding: 0 1px;
		height: 11px;
		@include lg {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			height: auto; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 1px;
			right: 1px;
			bottom: 0;
			@include lg {
				display: none; } }
		&-in {
			display: none;
			height: 100%;
			@include lg {
				display: block; } } }
	&__info {
		position: relative;
		padding: 8px 15px 0 1px;
		@include clr;
		@include lg {
			padding: 0;
			height: 60px;
			font-size: 0;
			line-height: 60px; } }
	&__value,
	&__description {
		padding: 5px 0;
		@include lg {
			display: inline-block;
			vertical-align: middle;
			padding: 0; }
		.sm-graph__box_text-right & {
			float: right;
			@include xl {
				float: none; } } }
	&__value {
		@include f(26, 25, null, 700);
		@include lg {
			padding: 0 0 0 18px;
			width: 70px;
			@include f(18, 24); } }
	&__description {
		@include f(14, 20);
		max-width: 160px;
		@include lg {
			padding: 0;
			width: 350px; }
		@include sm {
			width: 210px;
			@include f(14, 20); } } }
