.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	transition: transform .3s ease-in-out, background-color .3s ease-in-out;
	@include sm {
		margin-top: 28px; }
	&.is-fixed {
		@include sm {
			position: fixed;
			margin-top: 0;
			transform: translateY(-40px);
			height: 40px;
			background-color: rgba($black, 0.3); }
		&.is-show {
			transform: translateY(0); } }
	.inner {
		@include sm {
			padding: 0 29px; } }
	&__logo {
		display: none;
		.is-fixed & {
			display: block; } } }


.logo {
	float: left;
	margin-top: 16px;
	@include sm {
		display: none; }
	.is-fixed & {
		@include sm {
			margin-top: 10px;
			display: block; } } }
.menu {
	float: right;
	margin-top: 3px;
	@include sm {
		float: none;
		position: absolute;
		top: -28px;
		left: 100%;
		margin: 0;
		padding: 80px 0 50px;
		width: 100%;
		text-align: center;
		transition: transform 0.5s ease-in-out;
		background-color: $white;
		.is-open & {
			@include md {
				transform: translateX(-100%); } } }
	li {
		vertical-align: top;
		display: inline-block;
		@include sm {
			display: block; }
		&:not(:first-child) {
			margin-left: 20px;
			@include sm {
				margin: 20px 0 0; } } }
	a {
		position: relative;
		padding-top: 17px;
		display: block;
		@include f(13, 15, null, 700);
		color: $white;
		transition: color .3s ease-in-out;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			right: 0;
			height: 6px;
			border-radius: 2px;
			opacity: 0;
			transition: opacity .2s ease-in-out;
			background-color: $scooter; }
		&:after {
			position: absolute;
			content: "";
			top: 0;
			left: -10px;
			right: -10px;
			bottom: -10px; }
		@include sm {
			color: $black; }
		@include hover {
			&:before {
				opacity: 1; } } } }

.btn-menu {
	position: relative;
	float: right;
	margin-top: 19px;
	color: $white;
	z-index: 1;
	transition: color .5s ease-in-out;
	background-color: transparent;
	display: none;
	@include sm {
		display: block; }
	.is-open & {
		color: $black; }
	.is-fixed & {
		@include sm {
			margin-top: 13px; } }
	&__text,
	&__wrap {
		vertical-align: top;
		display: inline-block; }
	&__text {
		margin-right: 8px;
		@include f(10, 12, 80, 700);
		text-transform: uppercase; }
	&__wrap {
		position: relative;
		width: 20px;
		height: 14px;
		&:before,
		&:after,
		.btn-menu__line {
			position: absolute;
			content: "";
			left: 0;
			width: 100%;
			height: 2px;
			transition: background-color .5s ease-in-out, transform .5s ease-in-out, opacity .5s ease-in-out;
			background-color: $white;
			.is-open & {
				background-color: $black; } }
		&:before {
			top: 0;
			transform-origin: top left;
			.is-open & {
    			transform: translate3d(4px, -0.5px, 0) rotate(45deg); } }
		&:after {
			bottom: 0;
			transform-origin: bottom left;
			.is-open & {
    			transform: translate3d(4px, 1px, 0) rotate(-45deg); } }
		.btn-menu__line {
			top: 6px;
			.is-open & {
				transform: translateX(50%);
				opacity: 0; } } } }

.based {
	position: relative;
	float: right;
	margin: 14px 0 0 40px;
	padding-left: 26px;
	max-width: 183px;
	font-size: 12px;
	text-transform: uppercase;
	letter-spacing: .16em;
	line-height: 1.4;
	@include md {
		position: absolute;
		top: 50px;
		right: 20px; }
	@include sm {
		display: none; }
	&__logo {
		position: absolute;
		left: 0;
		top: 0;
		width: 18px;
		img {
			max-width: 100%;
			display: block; } } }

.apply {
	float: right;
	padding: 5px;
	width: 87px;
	text-align: center;
	color: $white;
	@include f(13, 18, null, 700);
	border-radius: 2px;
	background-color: $scooter;
	display: none;
	@include sm {
		display: block; }
	.is-fixed & {
		display: none; }
	&:not(:last-child) {
		margin-right: 30px; } }
