.sm-info {
	position: relative;
	padding: 6px 0 5px 27px;
	border-left: 4px solid $scooter;
	@include sm {
		padding: 16px 0 15px 27px;
		margin: 0 -9px 0 9px;
		font-size: 14px; }
	&.topic__sm-info {
		float: right;
		@include lg {
			float: none; } }
	&__text {
		@include sm {
			padding-left: 78px; }
		a {
			color: #60C9FF; } }
	&__wrap {
		margin-top: 19px; }
	&__logo,
	&__indicators,
	&__col {
		vertical-align: top;
		display: inline-block; }
	&__logo {
		@include sm {
			position: absolute;
			top: 15px;
			left: 19px;
			width: 72px; }
		img {
			max-width: 100%;
			display: block; } }
	&__indicators {
		padding-top: 4px;
		&:not(:first-child) {
			margin-left: 36px;
			@include sm {
				margin: 21px 0 0; } } }
	&__col {
		max-width: 200px;
		@include sm {
			font-size: 14px; }
		@include xxs {
			max-width: 115px; }
		&:not(:last-child) {
			margin-right: 19px; } }
	&__title {
		@include f(26, 30, null, 700);
		@include sm {
			font-size: 16px; } } }
